.sliderWrapper {
  width: 90%;
  position: relative;
  overflow: hidden;
  background: var(--background-color, #1a1a1a15);
  border-bottom-right-radius: 19%;
}

.sliderContainer {
  position: relative;
  width: 100%;
  max-width: 1820px;
  margin: 0 auto;
  max-height: var(--max-height, 660px);
  aspect-ratio: var(--aspect-ratio, 16/9);
}

.slide {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  transition: opacity 0.5s ease-in-out;
}

.image {
  width: 100%;
  height: 100%;
  object-fit: cover; /* Изменено с contain на cover */
  object-position: center;
  min-width: 100%;
  min-height: 100%;
}
.dotsContainer {
  position: absolute;
  bottom: 20px;
  left: 50%;
  transform: translateX(-50%);
  display: flex;
  gap: 10px;
  z-index: 2;
}

.dot {
  width: 12px;
  height: 12px;
  border-radius: 50%;
  border: none;
  background-color: rgba(0, 51, 51, 0.5);
  cursor: pointer;
  transition: all 0.3s ease;
}

.activeDot {
  background-color: rgba(153, 204, 51, 0.8);
  transform: scale(1.2);
}

.dot:hover {
  background-color: rgba(153, 204, 51, 0.8);
}

